
.App {
  flex: 1 1 auto;
  text-align: center;
  color: black;
  
 
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.App-header {
  background: url('https://images.unsplash.com/uploads/1413595244012e81f5c69/de0cc225?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=b019868c314903110f248b3a5cbd36e4&auto=format&fit=crop&w=1050&q=80') ;
  background-size: cover;
  padding: 20px;
  color:white
}
.App-gif {
  max-width: 50%;
  height: 100px;
  margin-bottom: 0;
  position: absolute;
  left: 20px;
  top: 5px;
}
.App-content {
  background: rgba(119, 117, 117, 0.7);
  padding: 20px;
  margin: 20px;
  border-radius: 10px;
  font-family: sans-serif;
}

.App-footer {
  background: rgba(0, 0, 0, 0.5);
  padding: 10px;
}

.App-footer a {
  color: #61dafb;
  margin: 0 10px;
  text-decoration: none;
}

.App-footer a:hover {
  text-decoration: underline;
}
.scrollable-container { /* Set the max height as needed */
  overflow-y: auto; /* Enable vertical scrolling */
  padding: 16px; /* Optional: add some padding */ /* Optional: add a border */
}
